
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},

components: {
Alert,
Loading,
Toast,
XHeader,
XInput,
XButton,
Group,
Grid,
GridItem,
PopupPicker,
Flexbox,
FlexboxItem,
Previewer
},

data() {
return {
apiHost:'',
routerId: '',
address: '',
lon:'',
lat:'',
city: '',
showUpload: false,
imgData: '',
imgUrl:'',
isShowImg: false,
pickerVal: [],
pickerList: [[]],
showSaveState: false,
saveText:'',
alertTitle:'',
alertContent:'',
showAlert:''
}
},

mounted(){
},

methods: {

scanCode(){
client.qr("qrCallBack");
},


qrCallBack(info){
this.routerId = info;
},


upload(){
client.takePhone("getPic");
},


getPic(src){
this.imgUrl = src;
this.showUpload = true;
},


getGps(){
const gpsStr = client.getGps();
const gps = gpsStr.split(",");
this.lon = gps[0];
this.lat = gps[1];
this.address = gps[2];
this.city = gps[3];
},


showImgDetail(){
this.isShowImg = true;
},


deleteImg(){
this.imgUrl = '';
this.isShowImg = false;
this.showUpload = false
},


save(){
const routerData = {
"routerId": this.routerId,
"lon": this.lon,
"lat": this.lat,
"addr": this.address,
"img": this.imgUrl
};

this.$http.post(apiHost + "router_stat/api/router/report",{
params: {
data: routerData
}
}).then(res => {
if(res.result == 1){
this.showSaveState = true;
this.saveText = '保存成功';
}else {
this.alertTitle = "保存失败";
this.alertContent = res.msg;
this.showAlert = true;
}
}).catch(res => {
this.alertTitle = "网络错误";
this.alertContent = res;
this.showAlert = true;
})
}
}
}



